import * as types from './types'

export default{
    [types.SELECTPLATFORM]:(state,n)=>{
        state.AccountList=state.AccountListOriginal;
        let AccountList=[];
        for(let i=0;i<state.AccountList.length;i++){
            if(state.AccountList[i].platform==n)
            {
                AccountList.push(state.AccountList[i])
            }
        }
        state.AccountList=AccountList;
    },
    [types.REMOVEROW]:(state,i)=>{
        state.ProductInfo.data1.splice(i,1);
        if(state.ProductInfo.data1.length==0){
            state.ProductInfo.data1.push({
                sku: '',
                status:'',
                productCName: '',
                inventory: '',
                gif:'是',
                count: '',
                gross: '',
                unitPrice1: '',
                unitPrice2: '',
                amount: ''
            });
        }
    },
    [types.ADDROW]:(state,i)=>{
        state.ProductInfo.data1.push({
            sku: '',
            status:'0',
            productCName: 'Iphone 12',
            inventory: '23',
            gif:'是',
            count: '2',
            gross: '1.68',
            unitPrice1: '10',
            unitPrice2: '12',
            amount: '23'
        });
    },
    [types.SBSEARCH]:(state)=>{
        let qs=[];
        for(let i=0;i<state.QuickShenbaoInfo.length;i++)
        {
            if(state.sbText==state.QuickShenbaoInfo[i].name)
            {
                qs.push(state.QuickShenbaoInfo[i])
            }
        }
        state.QuickShenbaoInfo=qs;
    },
    [types.ADDSBROW]:(state)=>{
        alert(12)
        state.ShenbaoInfo.data1.push({
            productCName: '',
            productEName: '',
            unitPrice: '',
            gross: '',
            count: '',
            address: ''
        });
    },
    [types.REMOVESBROW]:(state,i)=>{
        state.ShenbaoInfo.data1.splice(i,1);
        if(state.ShenbaoInfo.data1.length==0){
            state.ShenbaoInfo.data1.push({
                productCName: '',
                productEName: '',
                unitPrice: '',
                gross: '',
                count: '',
                address: ''
            });
        }
    }
}


