import * as types from './types'

export default{
    selectPlatform:({commit},n)=>{
        commit(types.SELECTPLATFORM,n)
    },
    removeRow:({commit},i)=>{
        commit(types.REMOVEROW,i)
    },
    addRow:({commit},i)=>{
        commit(types.ADDROW,i)
    },
    sbSearch:({commit,state},t)=>{
        commit(types.SBSEARCH,t)
    },
    addSBRow:({commit,state},t)=>{
        commit(types.ADDSBROW,t)
    },
    removeSBRow:({commit,state},t)=>{
        
        commit(types.REMOVESBROW,t)
    }
}


